@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.information-container {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  bottom: 0;
  right: 0;
  width: 95%;
  max-width: 820px;
  height: auto;
  padding: 10px;
  margin: 20px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  pointer-events: none;
}

.information-container div {
  padding: 5px;
}

.information-container .node-property-name {
  color: #afafaf;
  font-weight: 500;
  font-size: 0.8rem;
}

.information-container .node-property-value {
  font-size: 0.95rem;
}

.about-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  margin: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  color: #808080;
  overflow: hidden;
}

.about-container .about-button {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  width: 90px;
  height: 35px;
  border-radius: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.about-container .about-button:hover {
  background: hsla(0, 0%, 90%, 0.5);
}

.about-container .about-content {
  width: 90px;
  height: 0px;
  padding: 0px;
  opacity: 0;
  transition: width 0.5s ease-in-out 0.3s, height 0.5s ease-in-out 0.3s,
    padding 0s ease-in-out 0.3s, opacity 0.3s ease-in-out;
}
.about-container .about-content.openned {
  height: 258px;
  width: 490px;
  padding: 15px;
  opacity: 1;
  transition: width 0.6s ease-in-out, height 0.6s ease-in-out,
    padding 0s ease-in-out 0.6s, opacity 0.4s ease-in-out 0.6s;
}

.about-container .about-content p {
  margin: 5px;
}

.about-container .about-content p.about-title {
  font-weight: 500;
  font-size: 0.9rem;
  margin-bottom: 15px;
}

.about-container .about-content .github-button {
  width: 135px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  margin: 12px 5px 3px;
  padding: 10px;
  color: #333;
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: background 0.2s, color 0.2s;
}

.about-container .about-content .github-button:hover {
  color: #fff;
  background: #333;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  display: flex;
  flex: 1 1;
  min-height: 100%;
  max-width: 100%;
}

#root {
  display: flex;
  flex: 1 1;
}

